* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "basier-circle", sans-serif;
  color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
}

i {
  font-family: "custom-icons-font", sans-serif;
}

.btn {
  cursor: pointer;
  color: #fff;
  background: 0;
  border: 0;
}

.widget {
  position: relative;
  padding: 8px 10px;
  box-shadow: inset -5px -5px 250px rgba(255, 255, 255, 0.02);
  border-radius: 10px;
  z-index: 1;
}
.widget--hidden {
  display: none;
}
.widget:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 10px;
  background: radial-gradient(100% 100% at 0% 0%, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0) 100%);
  backdrop-filter: blur(42px);
  z-index: -1;
  pointer-events: none;
}
.widget:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 10px;
  box-sizing: content-box;
  border: 2px solid transparent;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.4)) border-box;
  -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  pointer-events: none;
}

.widget-rounded, .widget-rounded:after, .widget-rounded:before {
  border-radius: 20px;
}

.card {
  background: #EBEBEB;
  border-radius: 10px;
  padding: 40px;
  color: #000;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
}

.link {
  color: #fff;
  text-decoration: none;
}

.icon {
  cursor: pointer;
}

.modal {
  position: absolute;
  z-index: 9999;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  padding: 24px;
  display: flex;
  flex-direction: column;
}

.modal-overlay {
  z-index: 9000;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
}
.modal-overlay--settings {
  background: #DDE2E4;
}
.modal-overlay--add-city {
  z-index: 9999;
  background: #DDE2E4;
}
.modal-overlay--select-api-source {
  background: #DDE2E4;
  z-index: 10000;
}

.close-modal-btn {
  border: 0;
  background: none;
  position: absolute;
  right: 15px;
  top: -10px;
  cursor: pointer;
  color: #fff;
  font-size: 35px;
  display: flex;
  justify-content: flex-end;
  z-index: 9999;
}

.close-add-city-btn {
  color: #000;
}

.clickable:focus {
  border: 3px solid red;
}

.screen-wrapper {
  flex: 1;
  display: grid;
  place-content: center;
}
.screen-wrapper--bg--cloudy {
  background: linear-gradient(180deg, #0F2027 0%, #203A43 52.6%, #2C5364 100%);
}
.screen-wrapper--bg--normal {
  background: #DDE2E4;
}

.screen {
  height: 824px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}
@media screen and (min-width: 411px) {
  .screen {
    border: 1px solid #C4C4C4;
    border-left: 1px solid #EBEBEB;
  }
}
.screen__header {
  font-weight: normal;
  font-size: 30px;
}
.screen__header--black {
  color: #000;
}
.screen__date {
  color: #EBEBEB;
  font-size: 18px;
  margin-top: 5px;
}
.screen__cities {
  margin: 20px 0 0 0;
}
.screen__city {
  margin: 20px 0;
}
.screen__city-name {
  font-size: 30px;
  position: relative;
  left: -1px;
}
.screen__city-temperature {
  font-size: 40px;
  padding-right: 10px;
}
.screen__city-time {
  font-size: 0.9em;
  color: #EBEBEB;
}
.screen__city-info {
  display: flex;
  justify-content: space-between;
  color: #f2f2f2;
  margin-top: 30px;
}
.screen__city-temperature-range {
  color: #EBEBEB;
  font-size: 20px;
}
.screen__city-weather-condition {
  color: #EBEBEB;
}
.screen__title {
  font-weight: normal;
  display: flex;
  justify-content: space-between;
}
.screen__btn {
  margin: 20px 0 0 0;
  font-size: 3em;
  width: 1.5em;
  min-height: 1.5em;
}
.screen__add-btn {
  margin-top: 20px;
  align-self: flex-end;
  font-size: 30px;
  width: 75px;
  height: 75px;
  display: grid;
  place-content: center;
}
.screen__add-btn i {
  width: 75px;
  height: 75px;
  display: grid;
  place-content: center;
}
.screen__weather {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
}
.screen__temperature {
  margin-top: 15px;
  font-size: 40px;
}
.screen__weather-type {
  font-size: 20px;
  margin-bottom: 40px;
}
.screen__image {
  width: 160px;
}

.city-info-grid {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  grid-gap: 25px 20px;
  margin-bottom: 50px;
}
.city-info-grid__grid-item-wrapper {
  flex: 1 0 44%;
}
.city-info-grid__grid-item {
  flex: 1 0 44%;
  min-height: 100px;
}
.city-info-grid__grid-item:first-child {
  margin-right: 0px;
}
.city-info-grid__grid-item--small-margin {
  margin-bottom: 15px;
}
.city-info-grid__grid-item--no-margin {
  margin: 0;
}
.city-info-grid__grid-item--wide {
  flex: 0 0 100%;
  min-height: 120px;
}
.city-info-grid__widget-description {
  color: #EBEBEB;
  font-size: 15px;
  margin-bottom: 10px;
}
.city-info-grid__content-wrapper {
  font-size: 30px;
}
.city-info-grid__content-wrapper--margin-bottom {
  margin-bottom: 12px;
}
.city-info-grid__widget-additional {
  font-size: 15px;
}

.city-info-grid .city-info-grid__grid-item-wrapper .city-info-grid__grid-item-item {
  margin-right: 0px;
}

.screen-nav-wrapper {
  position: relative;
  height: 823px;
  width: 411px;
}
@media screen and (max-width: 411px) {
  .screen-nav-wrapper {
    width: 100vw;
  }
}

.navigation {
  position: absolute;
  z-index: 5;
  bottom: 0;
  right: 0;
  width: 100%;
  border-top: 1px solid #EBEBEB;
  background: rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  font-size: 2em;
}
@media screen and (max-device-width: 512px) {
  .navigation {
    position: fixed;
  }
}
.navigation__pages {
  display: flex;
}
.navigation__circle {
  font-size: 1.5em;
  color: #C4C4C4;
  width: 20px;
  margin: 0 5px;
  display: grid;
  place-content: center;
}
.navigation__circle--active {
  color: #fff;
}
.navigation__cities-link-wrapper {
  width: 42px;
  height: 42px;
  display: grid;
  place-content: center;
}
.navigation__smaller-icon {
  font-size: 25px;
}

.settings-modal-wrapper {
  position: absolute;
  z-index: 9999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 358px;
}

.card-select-api {
  margin-top: 30px;
}
.card-select-api select {
  width: 100%;
  margin-top: 10px;
  padding: 10px 0;
  font-size: 18px;
}

.settings {
  background: #EBEBEB;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}
.settings__item {
  display: flex;
  justify-content: space-between;
}
.settings__item-text {
  font-size: 20px;
  line-height: 24px;
}
.settings__toggle-icon {
  cursor: pointer;
  font-size: 2em;
}
.settings__toggle-icon--on {
  color: #689BFF;
}
.settings__toggle-icon--off {
  color: #C4C4C4;
}

.city-list {
  align-self: flex-start !important;
  display: flex;
  flex-direction: column;
}

.empty-city-list-container {
  padding: 60px 24px 24px 24px;
  display: flex;
  flex-direction: column;
}
.empty-city-list-container__text {
  font-size: 30px;
  text-align: justify;
  padding-right: 5px;
}
.empty-city-list-container + button {
  margin-right: 24px;
}

.close-settings-modal-btn {
  top: 130px;
  right: 8px;
  color: #000;
  z-index: 9999;
}
@media screen and (min-width: 410px) {
  .close-settings-modal-btn {
    right: 14px;
  }
}

.close-add-city-btn, .close-select-api-source-btn {
  top: 235px;
  right: 14px;
  z-index: 10000;
  color: #000;
}

.add-city, .select-api-source {
  margin-top: calc(50vh - 50%);
  z-index: 10000;
  padding: 1.7em;
}

.close-city-list-btn {
  top: 2px;
  z-index: 9999;
}

.add-city-form, .select-api-source-form {
  display: flex;
  flex-direction: column;
  z-index: 9999;
}
.add-city-form .input-wrapper, .select-api-source-form .input-wrapper {
  position: relative;
}
.add-city-form .input-wrapper input[type=text], .select-api-source-form .input-wrapper input[type=text] {
  display: block;
  width: 100%;
  background: linear-gradient(180deg, #DBDBDB 0%, rgba(228, 228, 228, 0.96) 100%);
  border-radius: 10px;
  border: 0;
  padding: 0.8em 2em 0.8em 2.6em;
  font-size: 0.95rem;
}
.add-city-form .input-wrapper input[type=text]:focus, .select-api-source-form .input-wrapper input[type=text]:focus {
  outline: none;
}
.add-city-form .input-wrapper .icon-wrapper, .select-api-source-form .input-wrapper .icon-wrapper {
  position: absolute;
  top: 8px;
  left: 5px;
}
.add-city-form .input-wrapper .icon-wrapper i, .select-api-source-form .input-wrapper .icon-wrapper i {
  font-size: 20px;
}
.add-city-form .input-wrapper label, .select-api-source-form .input-wrapper label {
  font-size: 18px;
}
.add-city-form .input-wrapper select, .select-api-source-form .input-wrapper select {
  width: 100%;
  margin-top: 10px;
  padding: 10px 0;
  font-size: 18px;
}
.add-city-form button, .select-api-source-form button {
  width: 100%;
  background: green;
  margin-top: 50px;
  padding: 0.3em 2em;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  font-size: 1.6rem;
}

.city-info {
  padding: 24px 24px 40px 24px;
}

@keyframes change-city {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.change-animate {
  animation-name: change-city;
  animation-duration: 0.4s;
  animation-direction: forwards;
}

.select-api-source-modal {
  z-index: 10000;
}